import React, { useState } from 'react';
import axios from 'axios'
import Profile from './components/Profile'
import config from './components/Config'

const baseUrl = 'https://api.github.com/users'
const App = () => {

    const [data, setData] = useState(null)
    const [repo, setRepo] = useState(null)
    const [filter, setFilter] = useState('')
    const [errorMessage, setErrorMessage] = useState('')

    const getUserData = () => {
        const url = `${baseUrl}/${filter}`
        console.log("url ", url)
        axios.get(url, { headers: config })
            .then(response => {
                setData(response.data)
            })
            .catch(error => {
                setErrorMessage('failed while fetching the user, enter another name')
            })
    }

    const getRepoData = () => {
        const repoUrl = `${baseUrl}/${filter}/repos`
        console.log("repo url", repoUrl)
        axios.get(repoUrl, { headers: config })
            .then(response => {
                console.log("response", response.data)
                setRepo(response.data)
            })
            .catch(error => {
                setErrorMessage('Failed fetching data')
            })
    }

    const handleSubmit = (event) => {
        setData(null)
        setRepo(null)
        setErrorMessage('')
        event.preventDefault()
        getUserData()
        getRepoData()
    }

    return (
        <div className="container">
            <h1>Github Profile Preview</h1>
            <form onSubmit={handleSubmit}>
                <input
                    value={filter}
                    onChange={(event) => setFilter(event.target.value)}
                />
                <button type="submit">search</button>
            </form>
            <h1>{errorMessage}</h1>
            {data !== null && repo !== null && <Profile data={data} repo={repo} />}
            <div className="credit">Icons made by <a href="https://www.flaticon.com/authors/pixel-perfect" title="Pixel perfect">Pixel perfect</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
        </div>
    )
}

export default App